body{
  background-color: rgb(250, 250, 250);
}
.nav{
  display: flex;
  background-color: rgb(255, 255, 255);
  height: 65px;
  align-items: center;
  gap: 100px;
}
.minazuki
{
  font-family: cursive;
  font-size: 23px;
  margin-left: 6vw;
  color: green;
}
.searchContainer 
{
  position: relative;
}
.searchBar
{
  background-color: white;
  border-width: 1px;
  border-radius: 20px;
  border-color: grey;
  border-style: solid;
  width: 47.5vw;
  height:40px;
  padding: 0px 18px; 
  box-sizing: border-box;
  outline: 0px;
  font-family: cursive;
  font-size: 14px;
}
.searchBar:focus
{
  border-color: green;
}
.searchBarIcon
{
  cursor: pointer;
  position: absolute;
  width: 30px;
  height: 20px;
  margin-right: 10px;
  right: 10px;
  top: 10px;
}
.searchBarLine
{
  position: absolute;
  margin-right: 10px;
  right: 50px;
  font-size: 25px;
  top: 0px;
  color: rgb(235, 235, 235);
}
.searchResultsIcon
{
  width: 30px;
  height: 20px;
  color: rgb(235, 235, 235);
  margin-right: 10px;
}
.searchResults
{
position: absolute;
height: auto;
max-height: 300px;
overflow-y: scroll;
width: 47vw;
border: 1px solid grey;
border-radius: 10px;
top: 50px;
}
.searchResults::-webkit-scrollbar 
{
  width: 2px;
}
.searchResults::-webkit-scrollbar-thumb 
{
  background-color: #555;
}
.searchResults::-webkit-scrollbar-button
{
  width: 1px;
}
.resultItem
{
  padding: 10px; 
}
.noResultItem
{
  padding: 10px; 
}
.resultItem:hover
{
  background-color: rgb(245, 245, 245);
  cursor: pointer;
}

@media screen and (max-width:680px)
{
  .nav
  {
    gap: 50px;
  }
}